import { css, ICssInput } from '@uifabric/utilities';
import { Checkbox } from 'office-ui-fabric-react/lib/Checkbox';
import { CommandBar } from 'office-ui-fabric-react/lib/CommandBar';
import {
  CheckboxVisibility,
  DetailsList,
  DetailsListLayoutMode,
  IColumn
} from 'office-ui-fabric-react/lib/DetailsList';
import { FocusZone } from 'office-ui-fabric-react/lib/FocusZone';
import { Icon } from 'office-ui-fabric-react/lib/Icon';
import { withResponsiveMode } from 'office-ui-fabric-react/lib/utilities/decorators/withResponsiveMode';
import * as React from 'react';
import { isNullOrUndefined } from 'util';
import {
  emptyTblSysRAs,
  getColAADSysRoleAssigns,
  ICellAadSysRoleAssign,
  IItemAadSysRoleAssign,
  ITblAadSysRoleAssigns,
  SEQUENCE_AAD_SSM_ROLES
} from '../../models/aadPAP/ssm';
import { GraphObjectTypes, IGraphObjectInfo } from '../../models/graph';
import { renderEveryOtherRow } from '../../utilities/fabric';
import { deepClone } from '../../utilities/object';
import { getResponsiveMode } from '../../utilities/responsiveMode';
import Loading from '../Common/Loading/Loading';
import ModalSearchObjects from '../SearchObjects/ModalSearchOjects/ModalSearchObjects';
import { ISSMProps, ISSMState } from './SSM.types';

const pageLayoutStyles = require('../../Styles/PageLayout.scoped.scss');
const listPageStyles = require('@microsoft/portal-app/lib/styling/patterns/ListPage.scoped.scss');
const ssmPgStyles = require('./SSM.scoped.scss');

@withResponsiveMode
export class SSM extends React.Component<ISSMProps, ISSMState> {
  private pageFocusRef = React.createRef<HTMLDivElement>();
  constructor(props: ISSMProps) {
    super(props);
    let initTblSysRAs: ITblAadSysRoleAssigns = { ...emptyTblSysRAs };
    if (
      !isNullOrUndefined(this.props.tblSysAssigns) &&
      this.props.tblSysAssigns.appId !== ''
    ) {
      initTblSysRAs = { ...this.props.tblSysAssigns };
    }

    this.state = {
      columns: getColAADSysRoleAssigns(
        getResponsiveMode(this.props.responsiveMode),
        this.props.t!
      ),
      tblSysAssigns: initTblSysRAs,
      showModal: false
    };
  }

  public componentDidMount() {
    this._focus();
  }
  public componentWillMount(): void {
    if (
      this.props.curAADNPD !== null &&
      !isNullOrUndefined(this.props.curAADNPD)
    ) {
      this.props.getAADSystemAssignments({ appId: this.props.curAADNPD.appId });
    }
  }

  public componentWillReceiveProps(nextProps: ISSMProps): void {
    if (
      nextProps.curAADNPD !== this.props.curAADNPD &&
      !isNullOrUndefined(nextProps.curAADNPD)
    ) {
      this.props.getAADSystemAssignments({ appId: nextProps.curAADNPD.appId });
    }

    if (
      nextProps.tblSysAssigns.appId !== this.state.tblSysAssigns.appId &&
      !nextProps.tblSysAssigns.isLoading
    ) {
      this.setState({
        tblSysAssigns: { ...nextProps.tblSysAssigns }
      });
    }
  }

  public render(): React.ReactNode {
    const { t } = this.props;
    return (
      <main
        data-automation-id="examplesList"
        className={css('ms-ListPage', listPageStyles.listPage)}
      >
        <div
          className={css(
            'ms-ListPageHeader',
            listPageStyles.pageHeader,
            pageLayoutStyles.pageHeader
          )}
        >
          <div
            role="heading"
            aria-level={1}
            tabIndex={-1}
            ref={this.pageFocusRef}
            className={css(
              'ms-PageTitle',
              listPageStyles.pageTitle,
              listPageStyles.pageSectionTitle
            )}
          >
            {t('SSM')}
          </div>
        </div>
        <CommandBar
          className={css(pageLayoutStyles.commandBar)}
          items={[
            {
              key: 'ITA NPD',
              name: t('Add', { context: 'capitalize' }),
              iconProps: {
                iconName: 'Add'
              },
              ariaLabel: 'Add Object as System Role',
              onClick: () => this.addObject()
            },
            {
              key: 'submit',
              name: 'Submit',
              iconProps: {
                iconName: 'Send'
              },
              onClick: () => this.updateSSM()
            }
          ]}
          farItems={[]}
        />
        <FocusZone>
          {!this.props.tblSysAssigns.isLoading ? (
            <div className={css(pageLayoutStyles.content)}>
              <DetailsList
                className={css(ssmPgStyles.listSSM)}
                items={this.state.tblSysAssigns.itemSysRoleAssigns}
                columns={this.state.columns}
                layoutMode={DetailsListLayoutMode.justified}
                onRenderItemColumn={this._renderItemColumn}
                onRenderRow={renderEveryOtherRow}
                checkboxVisibility={CheckboxVisibility.hidden}
                disableSelectionZone={true}
                ariaLabelForListHeader=""
                ariaLabelForGrid={this.props.t('System Role Assignment')}
              />
            </div>
          ) : (
            <Loading />
          )}
          <ModalSearchObjects
            showModal={this.state.showModal}
            selectObject={this.selectObject}
            t={this.props.t}
          />
        </FocusZone>
      </main>
    );
  }

  private _focus = () => {
    const node = this.pageFocusRef.current;
    if (node) {
      node.focus();
    }
  }

  private selectObject = (obj?: IGraphObjectInfo) => {
    this.setState({
      showModal: false
    });
    if (isNullOrUndefined(obj)) {
      return;
    }

    for (
      let i = 0;
      i < this.state.tblSysAssigns.itemSysRoleAssigns.length;
      i++
    ) {
      if (this.state.tblSysAssigns.itemSysRoleAssigns[i].id === obj.objectId) {
        return;
      }
    }

    const emptyRowSysRoles: ICellAadSysRoleAssign[] = [];
    for (let i = 0; i < SEQUENCE_AAD_SSM_ROLES.length; i++) {
      emptyRowSysRoles.push({
        isChanged: false,
        isClicked: false
      } as ICellAadSysRoleAssign);
    }

    const tblSysAssigns: ITblAadSysRoleAssigns = deepClone(
      this.state.tblSysAssigns
    );
    const itemSysRA: IItemAadSysRoleAssign = {
      id: obj.objectId,
      displayName: obj.displayName,
      graphObject: obj,
      rowSysRoles: emptyRowSysRoles
    };
    tblSysAssigns.itemSysRoleAssigns.push(itemSysRA);
    this.setState({
      tblSysAssigns: tblSysAssigns
    });
  }

  private updateSSM = () => {
    this.props.updateAADSystem({
      tblSysAssigns: { ...this.state.tblSysAssigns }
    });
    this.setState({
      tblSysAssigns: { ...emptyTblSysRAs }
    });
  }

  private addObject = () => {
    this.setState({ showModal: true });
  }

  private onClickRole = (iSysRole: number, iSysRA?: number) => {
    if (isNullOrUndefined(iSysRA)) {
      return;
    }
    const tbl: ITblAadSysRoleAssigns = JSON.parse(
      JSON.stringify(this.state.tblSysAssigns)
    );
    if (
      tbl.itemSysRoleAssigns[iSysRA].id !==
      tbl.itemSysRoleAssigns[iSysRA].graphObject.objectId
    ) {
      return;
    }
    tbl.itemSysRoleAssigns[iSysRA].rowSysRoles[iSysRole].isClicked = !tbl
      .itemSysRoleAssigns[iSysRA].rowSysRoles[iSysRole].isClicked;
    tbl.itemSysRoleAssigns[iSysRA].rowSysRoles[iSysRole].isChanged = true;
    this.setState({
      tblSysAssigns: tbl
    });
  }

  private _renderItemColumn = (
    item: IItemAadSysRoleAssign,
    index: number | undefined,
    column: IColumn | undefined
  ): React.ReactNode => {
    if (item.rowSysRoles === undefined || column === undefined) {
      return <React.Fragment />;
    }

    switch (column.key.substr(0, 5)) {
      case 'displ': {
        let objIconCss: ICssInput;
        let objIconName: string = '';
        switch (item.graphObject.objectType) {
          case GraphObjectTypes.User: {
            objIconName = 'Contact';
            objIconCss = ssmPgStyles.iconUser;
            break;
          }
          case GraphObjectTypes.Group: {
            objIconName = 'Group';
            objIconCss = ssmPgStyles.iconGroup;
            break;
          }
          case GraphObjectTypes.ServicePrincipal: {
            objIconName = 'AppIconDefault';
            objIconCss = ssmPgStyles.iconApp;
            break;
          }
        }
        return (
          <span>
            <Icon iconName={objIconName} className={css(objIconCss)} />
            {item[column.fieldName as keyof IItemAadSysRoleAssign] as string}
          </span>
        );
      }
      case 'sysRo': {
        const fieldName: string = column.fieldName as keyof IItemAadSysRoleAssign;
        const iSysRole: number = Number(
          fieldName.substr(fieldName.length - 1, 1)
        );
        const sysRole: ICellAadSysRoleAssign =
          item[fieldName.substr(0, fieldName.length - 1)][iSysRole];
        // let sysRoleIconCss: ICssInput;
        // sysRoleIconCss = ssmPgStyles.sysRoleIcon;
        // if (item.id !== item.graphObject.objectId) {
        //   sysRoleIconCss = ssmPgStyles.iconDisabled;
        // }
        // if (sysRole.isChanged) {
        //   sysRoleIconCss = ssmPgStyles.iconDirty;
        // }
        return (
          <div>
            <div className="system-role-checkbox">
              <Checkbox
                ariaLabel={`ssm display name is: ${
                  item.displayName
                } and column in ${column.name}`}
                checked={sysRole.isClicked}
                onChange={() => this.onClickRole(iSysRole, index)}
                disabled={item.id !== item.graphObject.objectId}
                tabIndex={0}
              />
              {/* <Icon
              iconName={`${sysRole.isClicked ? 'CheckboxComposite' : 'Checkbox'}`}
              className={css(sysRoleIconCss)}
              aria-hidden="true"
              onClick={() => this.onClickRole(iSysRole, index)}
            /> */}
            </div>
          </div>
        );
      }
      default:
        return (
          <span>
            {item[column.fieldName as keyof IItemAadSysRoleAssign] as string}
          </span>
        );
    }
  }
}



