import { createAction } from '@microsoft/portal-app/lib/redux/createAction';
import { translate } from 'react-i18next';
import { connect, Dispatch } from 'react-redux';
import { AuthzActions, IRootAuthzState } from '../../models';
import { IRoleAssignment } from '../../models/aadPAP/roleAssignment';
import { ITblAadSysRoleAssigns } from '../../models/aadPAP/ssm';
import { SSM } from './SSM';
import { IConnectedSSMProps, ISSMActions, ISSMMappedProps } from './SSM.types';

/**
 * Maps the external (i.e. own) props and the state to the properties of the ListPage control.
 * @param state The current redux state to map to props
 * @param ownProps The external properties to map to props
 * @returns The mapped properties that along with the actions
 * becomes the props (IListPageProps) for the ListPage control
 */
const mapStateToProps = (
  state: IRootAuthzState,
  _ownProps: IConnectedSSMProps
): ISSMMappedProps => {
  const { tblSysAssigns, currentNpd } = state.app;
  return {
    tblSysAssigns,
    curAADNPD: currentNpd
  };
};

/**
 * Maps the dispatch actions to the props for the ListPage control.
 * @param dispatch Dispatches actions to the store
 * @returns The mapped actions that along with the mapped properties
 * becomes the props (IListPageProps) for the ListPage control
 */
const mapDispatchToProps = (
  dispatch: Dispatch<IRootAuthzState>
): ISSMActions => {
  return {
    deleteRoleAssignment: createAction<IRoleAssignment>(AuthzActions.deleteRoleAssignment, dispatch),
    updateRoleAssignment: createAction<IRoleAssignment>(AuthzActions.updateRoleAssignment, dispatch),
    getAADSystemAssignments: createAction<{ appId: string }>(AuthzActions.getSSM, dispatch),
    updateAADSystem: createAction<{ tblSysAssigns: ITblAadSysRoleAssigns }>(AuthzActions.updateSSM, dispatch)
  };
};

/**
 * A redux connected ListPage control.
 * @description Supports translation
*/
export const ConnectedSSM = connect(
  mapStateToProps,
  mapDispatchToProps
)(translate('OneAuthZ')(SSM));


